*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
body{
  background-color: rgb(3, 19, 57);

  display: flex;
  align-items: start;
  justify-content: center;
  min-height: 100vh;
}
.todo-warper{
  width: 500px;
  height: auto;
  text-align: center;
  background-color: rgb(56, 67, 77);
  box-shadow: 3px 3px 3px rgb(255, 255, 255,0.5);
  border-radius: 5px;
  padding: 20px;
  margin: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.todo-warper h1{
  color: white;
  font-family: sans-serif;
  margin: 2%;
}
.todo-form{
  width: 100%;
  height: 100%;
  display: flex;
  margin: 2%;
  align-items: center;
  justify-content: center;
}
.todo-input{
  width: 80%;
  height: 10px;
  outline: none;
  margin-top: 2%;
  background-color:rgba(60, 60, 90,);
  padding: 20px;
  font-family: sans-serif;
  font-size: 16px;
  color: rgb(0, 0, 0);
  border:none;
}
.todo-btn{
  width: 15%;
  height: 40px;
  margin-top: 2%;
  background: blueviolet;
  color: white;
  border: 1px solid blueviolet;
  font-family: sans-serif;
  cursor: pointer;
}
.todo{
  width: 85%;
  height: 30%;
  padding: 10px;
  margin: 2.5%;
  background-color:blueviolet;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.todo p{
  font-family: sans-serif;
  color: white;
}
.todo button{
  width: 80px;
  height: 30px;
  cursor: pointer;
  border: 1px solid white;
  border-radius: 5px;
  background: rgb(32, 0, 32,0.7);
  color: white;
  font-family: sans-serif;
}

@media only screen and (max-width: 600px){
  .todo-warper{
    width: 350px;
    background-color: blue;
  }
}